.gallery-container {
  text-align: center;
  margin-top: 2rem;
}

.gallery {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  gap: 1rem;
}

.photo-container {
  position: relative;
  overflow: hidden;
  background-color: #444; /* Szare tło dla zdjęć */
  border-radius: 8px;
}

.photo-container img {
  display: block;
  max-width: 100%;
  height: auto;
  transition: transform 0.3s ease;
}

.photo-container:hover img {
  transform: scale(1.1);
}

.photo-overlay {
  position: absolute;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
  width: 100%;
  text-align: center;
  padding: 0.5rem 0;
  transform: translateY(100%);
  transition: transform 0.3s ease;
}

.photo-container:hover .photo-overlay {
  transform: translateY(0);
}
.fade-enter {
  opacity: 0;
  transform: scale(0.9);
}

.fade-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: opacity 500ms, transform 500ms;
}

.fade-exit {
  opacity: 1;
  transform: scale(1);
}

.fade-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 500ms, transform 500ms;
}
